import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ShopModuleComponent } from './shop-module.component';
import { ShopDashboardComponent } from './components/shop-dashboard/shop-dashboard.component';
import { ShopLayoutComponent } from './components/shop-layout/shop-layout.component';

const routes: Routes = [
  {
    path:'',
    component: ShopLayoutComponent,
    children: [
      {
        path:'',
        redirectTo: 'dashboard',
        pathMatch: 'full'
      },
      {
        path:'dashboard',
        component: ShopDashboardComponent,
      }, 
      {
        path: "product",
        loadChildren: () => import('../Product/product.module').then(m => m.ProductModule),
      },
      {
        path: "sale",
        loadChildren: () => import('../Invoice/sale/sale.module').then(m => m.SaleModule),
      },
      {
        path: "purchase",
        loadChildren: () => import('../purchase/purchase.module').then(m => m.PurchaseModule),
      },
      {
        path: "shop-profile-setting",
        loadChildren: () => import('../shop-module/components/shop-settings/shop-settings.module').then(m => m.ShopSettingsModule),
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ShopModuleRoutingModule { }
