import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ShopModuleRoutingModule } from './shop-module-routing.module';
import { ShopModuleComponent } from './shop-module.component';
import { RouterModule } from '@angular/router';
import { MaterialExampleModule } from 'src/app/material.module';
import { MatInputModule } from '@angular/material/input';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatNativeDateModule } from '@angular/material/core';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatMomentDateModule } from '@angular/material-moment-adapter';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { SharedModule } from '../shared/shared.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ShopLayoutComponent } from './components/shop-layout/shop-layout.component';
import { ShopDashboardComponent } from './components/shop-dashboard/shop-dashboard.component';


@NgModule({
  declarations: [
    ShopModuleComponent,
    ShopLayoutComponent,
    ShopDashboardComponent
  ],
  imports: [
    CommonModule,
    ShopModuleRoutingModule,
    RouterModule,
    MaterialExampleModule,
    MatInputModule,
    MatButtonModule,
    MatIconModule,
    MatNativeDateModule,
    MatFormFieldModule,
    MatDatepickerModule,
    MatMomentDateModule,    
    FormsModule,
    SharedModule,
    ReactiveFormsModule,
    NgbModule,
    BsDatepickerModule.forRoot(),
  ]
})
export class ShopModuleModule { }
